.menu__desktop ul{display:flex;flex-wrap:wrap;align-items:center;justify-content:center;list-style:none}.menu__desktop ul li button{border:none;background:none;color:#707070;cursor:pointer;font-family:"Questrial",sans-serif;padding:5px 20px;transition:all 0.5s ease}.menu__desktop ul li button:hover{background-color:#fc758c;color:#fff}.menu__desktop .active{background-color:#fc758c;color:#fff}

.menu__mobile ul{position:absolute;bottom:-200vh;left:0;width:100vw;height:100vh;background-color:#fff;z-index:3;display:flex;flex-direction:column;align-items:center;list-style:none;transition:all 0.5s ease}.visible ul{bottom:-100vh}.menu__mobile ul li{width:100%}.menu__mobile ul li button{border:none;width:100%;background:none;color:#707070;cursor:pointer;font-family:"Questrial",sans-serif;padding:15px 0;transition:all 0.5s ease}.menu__mobile .active{background-color:#fc758c;color:#fff;width:100%}.search{display:block;margin:0 auto}

.container{padding:0 15%}.container__header{display:flex;align-items:center;flex-direction:column;margin-bottom:40px}.views{margin:60px 10px;display:flex}.views button{border:none;cursor:pointer;background:none;margin:0 10px;opacity:0.5}.views button.active{opacity:1}.container button.btn-primary{display:block;margin:20px auto 60px}.loading{border:4px solid #c3d0d8;width:36px;height:36px;border-radius:50%;border-left-color:#fc758c;margin:0 auto;-webkit-animation:spin 1s ease infinite;animation:spin 1s ease infinite}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.footer__container{display:flex;flex-direction:column;align-items:center;padding-bottom:100px}.footer__container p{font-family:"Questrial",sans-serif;font-size:0.8rem;color:#c3d0d8;margin-bottom:30px;letter-spacing:2px}.footer__container p span{color:#fc758c}

*{margin:0;padding:0}header{background-color:#fff;z-index:2}.menu{display:flex;align-items:center;flex-wrap:wrap;justify-content:space-between;max-width:85%;margin:3rem auto}.menu__logo{align-items:center;cursor:pointer;display:flex;grid-gap:1rem;gap:1rem;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.menu__logo-name{color:#707070;font-family:"Montserrat",sans-serif;font-size:1.5rem;font-weight:700;letter-spacing:5px}.menu__logo-slogan{color:#c3d0d8;font-family:"Montserrat",sans-serif;font-size:0.6rem;letter-spacing:3px}.menu-icon{width:1.5rem}@media screen and (max-width: 768px){header{position:-webkit-sticky;position:sticky;top:0}.menu{padding-top:1rem;padding-bottom:1rem;margin-top:1rem}}

.banner{background-color:#fc758c;background-size:cover;width:90%;margin:0 auto;height:70vh;min-height:300px;position:relative}.banner__content{color:#fff;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center}.banner__content h1{letter-spacing:5px;font-family:"Montserrat",sans-serif;font-size:2rem}.banner__content p{letter-spacing:3px;line-height:1rem;font-family:"Questrial",sans-serif;font-size:0.7rem;margin:20px 0}

.btn,.btn-primary,.btn-neutral{border:none;cursor:pointer;padding:10px 25px;transition:all 0.3s ease;font-family:"Questrial",sans-serif}.btn-naked{border:none;cursor:pointer;background:none}.btn-primary{background-color:#fc758c;color:#fff;font-size:0.8rem}.btn-neutral{background-color:#fff;color:#fc758c;font-size:0.8rem}.tooltip{position:relative;display:inline-block}.tooltip .tooltiptext{visibility:hidden;width:100px;background-color:#707070;color:#fff;font-family:"Questrial",sans-serif;font-size:0.8rem;text-align:center;border-radius:6px;padding:5px 0px;position:absolute;z-index:1;bottom:150%;left:50%;margin-left:-50px;opacity:0;transition:opacity 0.3s}.tooltip .tooltiptext::after{content:"";position:absolute;top:100%;left:50%;margin-left:-5px;border-width:5px;border-style:solid;border-color:#707070 transparent transparent transparent}@media screen and (min-width: 480px){.btn-neutral:hover,.btn-primary:hover{transform:scale(1.1);box-shadow:3px 2px #c3d0d8}.tooltip:hover .tooltiptext{visibility:visible;opacity:1}}

